import {
    Component,
    OnInit,
    OnDestroy,
    ViewChild,
    Input,
    OnChanges,
    SimpleChanges
} from "@angular/core";
import { Site } from "../../../api";
import { StoreService } from "../../../shared/services/store.service";
import { ModalComponent } from "../../../shared/components/modal/modal.component";
import { ImageLibComponent } from "../../../shared/components/image-lib/image-lib.component";
import { Router } from "@angular/router";
import { AppService } from "../../../global/services/app.service";
import {
    FormulairesService,
    FormulairePagination,
    FormulaireResponsesService,
    FormulaireReponsePagination,
    FormulaireReponse,
    Formulaire
} from "../../../api-form";
import { ErrorsService } from "../../../global/services/errors.service";
import { CommentaireService } from "../../services/commentaire.service";
import { FormGroup, FormControl } from "@angular/forms";
import { Observable } from "rxjs/Observable";
import { Subscription } from "rxjs/Subscription";

@Component({
    selector: "formulaire-list",
    templateUrl: "./list.component.html",
    styleUrls: ["./list.component.scss"]
})
export class ListComponent implements OnInit, OnDestroy, OnChanges {
    @ViewChild("modalDomain") modalDomain: ModalComponent;
    @ViewChild("modalDeleteSite") modalDeleteSite: ModalComponent;
    @ViewChild("modalNotFound") modalNotFound: ModalComponent;
    @ViewChild(ImageLibComponent) imglib: ImageLibComponent;
    @ViewChild("modalDeletePost")
    modalDeletePost: ModalComponent;
    @Input() moduleName: string = null;
    @Input() module = {
        labelEntity: "",
        pathActivate: ""
    };
    @Input() msgModalActivate = "";
    @Input() msgBtnActivate = "";
    @Input() pathActivate = "";

    // Array des sites de l'user
    sites: Site[];

    // determine l'ordre du listing des sites
    listConf = {
        order: ""
    };

    // indique si les sites sont chargés
    areLoadedSites: boolean = false;
    state: any = {};

    btnEditArticle = {
        label: "Editer",
        icon: { active: true, className: "fas fa-pencil-alt" },
        className: "we-btn we-btn-block we-btn-secondary we-mb-2"
    };

    btnDeleteArticle = {
        label: "Supprimer",
        icon: { active: true, className: "far fa-trash-alt" },
        className: "we-btn we-btn-block we-btn-danger"
    };

    pagination: FormulaireReponsePagination = { items: [], totalItems: 0 };
    pagination_loaded = false;

    page = 1;

    actions = {};

    itemToDelete = null;
    reloadPublish = null;
    loadedList = false;

    comments: FormulaireReponse[] = [];
    form: Formulaire;
    TabKeys = [];
    formulaireReponses: FormulaireReponse[];
    isLoaded = false;

    ctrls: FormGroup[] = [];
    loadersOnline = [];
    loadedForm = false;

    // On cancel les requetes ajax quand on change de route/params
    form$: Subscription;
    formReponse$: Subscription;
    enableModalNotFound = true;

    constructor(
        private FormApi: FormulairesService,
        private FormResponsesApi: FormulaireResponsesService,
        public storeService: StoreService,
        private router: Router,
        public appService: AppService,
        private errorsService: ErrorsService,
        private commentaireService: CommentaireService,
        private store: StoreService
    ) {}

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.moduleName) {
            if (this.form$) {
                this.form$.unsubscribe();
            }
            if (this.formReponse$) {
                this.formReponse$.unsubscribe();
            }
            this.form$ = null;
            this.pagination = { items: [] };
            this.pagination_loaded = false;
            this.page = 1;
            this.getPublish();
        }
    }

    ngOnInit() {}

    onChangePage($event) {
        this.page = $event.page;
        this.getPublish();
    }

    wantDelete(item, publish) {
        this.itemToDelete = item;
        this.reloadPublish = publish;
        this.modalDeletePost.open();
    }

    deletePost() {
        const idToDelete = this.itemToDelete.id;
        this.FormResponsesApi.apiFormulairesReponseReponseDelete(
            String(idToDelete)
        ).subscribe(() => {
            this.loadedList = false;
            this.getPublish();
            this.modalDeletePost.close();
        });
    }

    getPublish() {
        this.loadedForm = false;
        const module = this.store.modules.find(
            m => m.name === this.moduleName
        ) || { id: null };
        let formId = this.store.curSite.uids.find(u => u.nom === "form");
        if (!this.form$) {
            this.form$ = this.FormApi.apiSitesSiteFormulairesModuleIdModuleGet(
                formId.distant_id.toString(),
                module.id
            ).subscribe(
                e => {
                    const form = e;
                    let groupes = [];
                    if (typeof form["groupes"] != "undefined") {
                        form["groupesF"] = JSON.parse(form["groupes"]);
                    } else {
                        form["groupesF"] = groupes;
                    }
                    this.form = e;
                    this.loadedForm = true;
                    this.getReponses();
                },
                error => {
                    if (error.status === 404) {
                        // alert("Les commentaires ne sont pas activés pour ce module.");
                        // this.router.navigateByUrl("/commentaire");
                        this.modalNotFound.open();
                    } else {
                        this.errorsService.handleErrorHttp(error);
                    }
                }
            );
        } else {
            this.getReponses();
        }
    }

    getReponses() {
        if (this.form) {
            this.isLoaded = false;
            this.formReponse$ = this.FormResponsesApi.apiFormulairesSlugReponsesGet(
                this.form.id.toString(),
                this.page
            ).subscribe(rep => {
                this.pagination = rep;
                this.pagination.page = Number(this.pagination.page);
                let articles = rep.items;
                articles.map((article, i) => {
                    article["data"] = JSON.parse(article.data);

                    this.ctrls[i] = new FormGroup({
                        online: new FormControl(article.online)
                    });
                    this.ctrls[i].valueChanges
                        .debounceTime(500)
                        .distinctUntilChanged()
                        .subscribe(data => {
                            const { online } = data;
                            if (online !== article.online) {
                                this.loadersOnline[i] = true;
                                this.toggleOnline(
                                    { online, ...article },
                                    i,
                                    () => {
                                        this.loadersOnline[i] = false;
                                    }
                                );
                            }
                        });
                });

                if (articles.length > 0) {
                    this.TabKeys = Object.keys(articles[0]["data"]);
                    this.TabKeys = this.TabKeys.filter(
                        k => k !== "recaptcha" && k !== "rgpd" && k !== "Email"
                    );
                }

                this.pagination_loaded = true;
                this.loadedList = true;
                this.formulaireReponses = articles || [];
                this.isLoaded = true;
                this.loadedForm = true;
            });
        }
    }

    ngOnDestroy() {
        // prevent memory leak when component destroyed
        this.state = null;
    }

    clickOnRowItem(event: Event, item) {
        const canNavigate =
            event.srcElement &&
            event.srcElement["nodeName"] !== "BUTTON" &&
            event.srcElement["nodeName"] !== "I";

        if (canNavigate) {
            this.router.navigate([
                "/formulaire",
                "formulaires",
                "edit",
                item.id
            ]);
        }
    }

    // Comme l'email dans data est supprimer par le pipe
    // lorsqu'on met a jour la première fois, cela envoie donc la data sans l'email
    // donc plus d'email dans data quand on recupere le listing des commentaires sur le site des clients
    // passage obligatoire par ici pour mettre en ligne le commentaire
    toggleOnline(item: FormulaireReponse, index: number, onSuccess: Function) {
        const { id, created, email, ...formRep } = item;
        item.online = formRep.online = !formRep.online;
        formRep.data = JSON.stringify(formRep.data);
        delete formRep["module_resource"];

        return this.FormResponsesApi.apiFormulairesSlugReponsesFormReponsePut(
            formRep,
            this.form.id.toString(),
            id.toString()
        ).subscribe(_formRep => {
            this.formulaireReponses[index].online = _formRep.online;
            onSuccess();
        });
    }

    goToPageEntity() {
        this.enableModalNotFound = false; // pour eviter de trigger goHome()
        setTimeout(() => {
            // obliger sinon marche pas
            this.router
                .navigateByUrl(`${this.module.pathActivate}`)
                .then(() => {
                    this.store.openCustomizer();
                });
        }, 0);
    }

    goHome() {
        this.router.navigateByUrl("/commentaire");
    }
}



