import {
    Component,
    OnInit,
    ViewChild,
    TemplateRef,
    ChangeDetectorRef
} from "@angular/core";

import { MenuItem } from "../../../shared/classes/menu";
import { ModalComponent } from "../../../shared/components/modal/modal.component";
import { AppConf } from "../../../global/app.conf";
import { StoreService } from "../../../shared/services/store.service";

declare var $: any;

@Component({
    selector: "blog-navigation-sidebar",
    templateUrl: "./navigation-sidebar.component.html",
    styleUrls: ["./navigation-sidebar.component.scss"]
})
export class NavigationSidebarComponent implements OnInit {
    maxCategories = 10;

    @ViewChild("tplPost") tplPost: TemplateRef<any>;
    @ViewChild("tplCat") tplCat: TemplateRef<any>;
    @ViewChild("modalDeletePost")
    modalDeletePost: ModalComponent;

    itemToDelete = null;

    actions = {};

    itemsCats: MenuItem[] = [
        {
            id: "blog",
            iconL: { active: true, className: "fal fa-newspaper" },
            iconR: AppConf.NAV_ITEM.iconR,
            name: "Blog",
            routerLink: "/commentaire/blog",
            className: AppConf.NAV_ITEM.css,
            linkFill: true,
            type: "page"
        },
        {
            id: "shop",
            iconL: { active: true, className: "fal fa-shopping-cart" },
            iconR: AppConf.NAV_ITEM.iconR,
            name: "E-Commerce",
            routerLink: "/commentaire/shop",
            className: AppConf.NAV_ITEM.css,
            linkFill: true,
            type: "page"
        }
        // {
        //     id: "blog_list_cats",
        //     iconL: { active: true, className: "fal fa-copy" },
        //     iconR: AppConf.NAV_ITEM.iconR,
        //     name: "Catégories",
        //     className: AppConf.NAV_ITEM.css,
        //     routerLink: "/blog/categories",
        //     linkFill: true,
        //     type: "page"
        // }
    ];

    constructor() {}

    ngOnInit() {
        // this.itemsCats = this.itemsCats.map(item => {
        //     item.visible = this.store.modules.find(m => m.name === item.id) ? true : false;
        //     return item;
        // });
    }

    handleAction($e) {
        return $e && this.actions[$e.action] && this.actions[$e.action]($e);
    }
}



