import { Component, OnInit, ViewChild, TemplateRef, AfterViewInit, ChangeDetectorRef } from "@angular/core";
import { MenuItem } from "../../../shared/classes/menu";
import { Router, ActivatedRoute } from "@angular/router";
import { Subscription } from "rxjs/Subscription";
import { ListComponent } from "../../components/list/list.component";
import { CommentaireService } from "../../services/commentaire.service";
import { StoreService } from "../../../shared/services/store.service";

declare var $: any;

@Component({
    selector: "blog-accueil",
    templateUrl: "./accueil.component.html",
    styleUrls: ["./accueil.component.scss"]
})
export class AccueilComponent implements OnInit {
    subscription: Subscription;

    @ViewChild(ListComponent) blogList: ListComponent;

    actions = {
        refresh_list_accueil: data => this.refreshList()
    };

    moduleName: string = null;

    modules = {
        shop: {
            // le label dans le header de la table (produit, article..)
            labelEntity: "Produit",
            // chemin pour activer les commentaires
            pathActivate: "/shop/apercu/produit"
        },
        blog: {
            labelEntity: "Article",
            pathActivate: "/blog/apercu/article"
        }
    };

    moduleSelected = this.modules[0];

    constructor(
        private commentaireService: CommentaireService,
        private router: Router,
        private activeRoute: ActivatedRoute,
        private store: StoreService
    ) {}

    ngOnInit() {
        this.activeRoute.params.subscribe(routeParams => {
            if (this.moduleName !== "all" && !this.store.modules.find(m => m.name === routeParams.name_module)) {
                this.router.navigateByUrl("/");
            } else {
                this.moduleSelected = this.modules[routeParams.name_module];
                this.moduleName = routeParams.name_module;
            }
        });

        this.subscription = this.commentaireService.events.subscribe(
            $e => this.actions[$e.action] && this.actions[$e.action]($e)
        );
    }

    refreshList() {
        // this.blogList.getPublish();
        // this.blogList.getUnPublish();
    }

    goTo(where: string) {
        if (where === "home") {
            this.router.navigate(["/"]);
        }
    }
}



