import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { MainComponent } from "./components/main/main.component";
import { AccueilComponent } from "./pages/accueil/accueil.component";
import { PendingChangesGuard } from "../shared/_guards/pending-changes.guard";

const routes: Routes = [
    {
        path: "",
        component: MainComponent,
        canDeactivate: [PendingChangesGuard],
        children: [
            // { path: "recents", component: AccueilComponent },
            { path: ":name_module", component: AccueilComponent }
            // { path: "posts", component: AccueilComponent },
            // {
            //     path: "commentaires/edit/:id",
            //     component: EditComponent
            // },
            // {
            //     path: "commentaires/nouveau",
            //     component: EditComponent
            // },
            // { path: "categories", component: CategoriesListComponent },
            // { path: "**", redirectTo: "shop" }
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class CommentaireRoutingModule {}



