import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";

import { CommentaireRoutingModule } from "./commentaire-routing.module";
import { MainComponent } from "./components/main/main.component";
import { AccueilComponent } from "./pages/accueil/accueil.component";
import { SharedModule } from "../shared/shared.module";
import { ListComponent } from "./components/list/list.component";
import { PaginationComponent } from "./components/pagination/pagination.component";
import { CommentaireService } from "./services/commentaire.service";
import { NavigationSidebarComponent } from "./components/navigation-sidebar/navigation-sidebar.component";

@NgModule({
    imports: [CommonModule, CommentaireRoutingModule, SharedModule],
    declarations: [
        MainComponent,
        AccueilComponent,
        ListComponent,
        PaginationComponent,
        NavigationSidebarComponent
    ],
    providers: [CommentaireService]
})
export class CommentaireModule {}



